html,
body,
#root {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
    background-color: #121212;
}

* {
    box-sizing: border-box;
}

.timer_widget{
    position: absolute;
    pointer-events: none;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}

.wave_widget{
    position: absolute;
    pointer-events: none;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;

    .console{
        color: white;
        display: flex;
        gap: 10px;
        position: absolute;
        top: 15%;

        .wave_prompt{
            background-color: rgba(0, 0, 0, 0.5);
            padding: 5px 10px;
            border-radius: 5px;
            font-family: 'Montserrat';
            color: gold;
            font-size: 0.8vw;

            &::after{
                content: ' wants to talk!';
                color: white;
            }
        }
    }

    .hand{
        position: absolute;
        cursor: pointer;
        pointer-events: all;
        top: 5%;
        padding: 10px;
        border-radius: 10px;

        min-width: 50px;
        width: 5%;

        svg{
            position: relative;
        }

        &:hover{
            background-color: rgba(255, 255, 255, 0.3);

        }
    }
}

.dashboard{
    padding: 25px;

    .widget{
        padding: 10px;
        width: min-content;
        border-radius: 10px;
        background-color: #1E1E1E;
        cursor: pointer;

        &:hover{
            background-color: #2C2C2C;
        }
    }
}

.interface {

    height: 100%;
    width: 100%;

    display: flex;

    .content{
        display: flex;
        flex-direction: column;
        flex:  1 1;
        overflow: auto;

        &::-webkit-scrollbar{
            display: none;
        }

        .container {
            display: flex;
            max-height: 100%;
            justify-content: center;
            align-items: center;

            .display {
                position: relative;
                height: fit-content;
                max-height: 100%;
                max-width: 100%;
                aspect-ratio: 16/9;

                display: flex;
                justify-content: center;
                flex: 1 1;

                border: none;
                background-color: black;

                .open_chat {
                    position: absolute;
                    top: 15px;
                    right: 15px;
                    height: 30px;
                    width: 30px;
                    fill: white;
                    padding: 5px;
                    padding-top: 4px;
                    cursor: pointer;
                    border-radius: 5px;
                    transform: rotate(180deg);
        
                    &:hover {
                        background-color: rgba(255, 255, 255, 0.3);
                    }
        
                    @media screen and (max-width: 920px) {
                        visibility: hidden;
                    }
                }
        
                .vmix_tab {
                    width: auto; 
                    height: 100%; 
                    aspect-ratio: 16 / 9; 
                    border: none;
                    box-sizing: content-box;
                }
            }
        }
    }

    .chat {
        display: flex;
        flex-direction: column;
        width: 339px;
        height: 100%;
        background-color: #1E1E1E;
        border-left: 1px solid #2C2C2C;

        @media screen and (max-width: 920px) {
            display: none;
        }

        .title {
            position: relative;
            height: 60px;
            flex: 0 0 60px;
            display: flex;
            justify-content: center;
            align-items: center;
            color: white;
            font-weight: 500;
            font-size: 20px;
            border-bottom: 1px solid #2C2C2C;

            .close_chat {
                position: absolute;
                top: 15px;
                left: 15px;
                height: 30px;
                width: 30px;
                fill: white;
                padding: 5px;
                padding-top: 4px;
                cursor: pointer;
                border-radius: 5px;

                &:hover {
                    background-color: #2C2C2C;
                }
            }
        }

        .chat_container {
            flex-grow: 1;
            overflow: auto;
            padding: 10px;

            .chat_feed {
                height: 100%;
                display: flex;
                flex-direction: column-reverse;
                overflow: auto;

                &::-webkit-scrollbar {
                    width: 6px;
                    /* Width of the scrollbar */
                }

                &::-webkit-scrollbar-track {
                    background: transparent;
                    /* Background of the scrollbar track */
                }

                &::-webkit-scrollbar-thumb {
                    background: #2C2C2C;
                    /* Scrollbar thumb color */
                    border-radius: 10px;
                    /* Rounded corners for the thumb */
                }

            }

            .message {

                padding: 5px 10px;
                border-radius: 5px;

                &:hover {
                    background-color: #2C2C2C;
                }

                .username {
                    display: inline;
                    color: white;
                    font-weight: 500;
                    font-size: 13px;

                }

                .content {
                    display: inline;
                    text-wrap: wrap;
                    font-size: 13px;
                    color: white;

                    &::before {
                        content: ': ';
                    }
                }

            }
        }
    }
}

.login {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
    }

    .border{
        background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
        height: 40px;
        width: 350px;
        padding: 1px;
        border-radius: 5px;
    }

    .title {
        color: white;
        font-size: 30px;
        font-weight: 600;
        margin-bottom: 15px;
    }

    input {
        background-color: #000;
        height: 100%;
        width: 100%;
        border: none;
        border-radius: 5px;
        outline: none;
        color: white;
        display: block;
        margin-right: 0;
        padding: 0 10px;
    }

    .invalid_email{
        font-size: 10px;
        font-weight: 600;
        width: 100%;
        background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
        -webkit-background-clip: text;
        color: transparent;
    }

    .validate_login {
        background: linear-gradient(149deg, #1e90ff 0%, #6a0dad 96.86%);
        color: white;
        border: none;
        border-radius: 25px;
        height: 40px;
        width: 350px;
        margin-top: 15px;
        cursor: pointer;

        &:hover {
            opacity: 0.8;
        }
    }
}